import React, {useEffect, useState} from 'react';
import {View, ScrollView, Dimensions, useWindowDimensions} from 'react-native';
import styles from './styles';
import {useTranslation} from 'react-i18next';
import TextCM from '../Text';

type Props = {
  tableHeaders: string[];
  tableData: any[];
  stickyColumnLeftIndex?: number;
  stickyColumnRightIndex?: number;
  stickyLeft?: boolean;
  stickyRight?: boolean;
  isPaging?: boolean;
};

const TableCM = ({
  tableHeaders,
  tableData,
  stickyColumnLeftIndex = 0,
  stickyColumnRightIndex,
  stickyLeft,
  stickyRight,
  isPaging = true,
}: Props) => {
  const {t} = useTranslation();
  const {width} = useWindowDimensions();
  const [widthOfStickey, setWidthOfStickey] = useState(0);
  return (
    <View style={styles.container}>
      <View style={styles.tableBody}>
        {/* Sticky Name Column */}
        {stickyLeft &&
          stickyColumnLeftIndex &&
          tableHeaders
            .slice(-stickyColumnLeftIndex)
            .reverse()
            .map((header, index) => (
              <View
                onLayout={e => {
                  setWidthOfStickey(e.nativeEvent.layout.width);
                }}
                key={header}
                style={[
                  styles.stickyColumnScroll,
                  {
                    right: index * widthOfStickey,
                  },
                  1 === stickyColumnLeftIndex && styles.box,
                ]}>
                {/* Sticky Header Column */}
                <View
                  style={[
                    styles.stickyHeader,
                    header === 'action' && {alignItems: 'center'},
                  ]}>
                  <TextCM style={styles.headerText}>{t(`TK.${header}`)}</TextCM>
                </View>
                {/* Sticky Data Column */}
                {tableData?.map((rowData, index) => (
                  <View
                    key={index}
                    style={[
                      styles.stickyColumn,
                      header === 'action' && {alignItems: 'center'},
                    ]}>
                    {typeof (rowData as any)[header] !== 'object' ? (
                      <TextCM style={styles.cellText}>
                        {(rowData as any)[header]}
                      </TextCM>
                    ) : (
                      (rowData as any)[header]
                    )}
                  </View>
                ))}
              </View>
            ))}

        {/* Scrollable Header + Columns */}
        <ScrollView horizontal>
          {/* Scrollable Columns */}
          <View
            style={[
              styles.row,
              !stickyLeft && {
                marginRight: 0,
              },
            ]}>
            {tableHeaders
              .slice(0, tableHeaders.length - stickyColumnLeftIndex)
              .map((header, index) => (
                <View key={index}>
                  <View
                    style={[
                      styles.stickyHeader,
                      header === 'action' && {alignItems: 'center'},
                      {
                        minWidth:
                          header === 'STT'
                            ? 30
                            : (width - 30) /
                              (tableHeaders.length - stickyColumnLeftIndex - 1),
                      },
                    ]}>
                    <TextCM style={styles.headerText}>
                      {t(`TK.${header}`)}
                    </TextCM>
                  </View>

                  {tableData?.map((rowData, index) => (
                    <View
                      key={index}
                      style={[
                        styles.stickyColumn,
                        header === 'action' && {alignItems: 'center'},
                        {
                          minWidth:
                            header === 'STT'
                              ? 30
                              : (width - 30) /
                                (tableHeaders.length -
                                  stickyColumnLeftIndex -
                                  1),
                        },
                      ]}>
                      {typeof (rowData as any)[header] !== 'object' ? (
                        <TextCM style={styles.cellText}>
                          {(rowData as any)[header]}
                        </TextCM>
                      ) : (
                        (rowData as any)[header]
                      )}
                    </View>
                  ))}
                </View>
              ))}
          </View>
        </ScrollView>
      </View>
    </View>
  );
};

export default TableCM;
