import {SafeAreaView} from 'react-native-safe-area-context';
import WebView, {WebViewMessageEvent} from 'react-native-webview';
import {String} from '../../../constants';
import {Alert, View} from 'react-native';
import ButtonCM from '../Button';
import styles from './styles';
import {useEffect, useRef, useState} from 'react';
import {useAppSelector} from '../../../hooks/useRedux';
import TextCM from '../Text';
import {SizeDP} from '../../../constants/Size';
import {DateUtils} from '../../../utils';
import Toast from 'react-native-toast-message';
import {useReviewServices} from '../../../hooks/apiHooks/useReviewTicketServices';
import {useTranslation} from 'react-i18next';
import {getConfig} from '../../../services/httpClient';
import HeaderCM from '../Header/HeaderCM';
import {OverflowMenuCM} from '../OverflowMenu';
import {Divider, IndexPath} from '@ui-kitten/components';
interface IData {
  _userTaskId: string;
  _ticketId: string;
  formId: string;
}

type Props = {
  versionId: any;
  data: IData;
  readOnly: boolean;
  isShowNoti?: boolean;
  isOnlyView?: boolean;
  viewFromDoc?: boolean;
  onClose: () => void;
  handleSubmit?: () => void;
  nameHeader: string;
  envelopeEnvNo?: string;
};

const ViewFormScreen = ({
  versionId,
  isShowNoti = false,
  data,
  readOnly,
  isOnlyView,
  viewFromDoc = false,
  onClose,
  handleSubmit,
  nameHeader,
  envelopeEnvNo,
}: Props) => {
  const {t} = useTranslation();
  const webviewRef = useRef<WebView>(null);

  const {accessToken} = useAppSelector(state => state.common);

  const [versionFromSelect, setVersionFromSelect] = useState<any>();
  const [listFrom, setListFrom] = useState<any[]>([]);
  const [isDisable, setIsDisable] = useState<boolean>(true);
  const [selectedIndex, setSelectedIndex] = useState<IndexPath | undefined>();
  const [isSaveFormNotSubmitted, setIsSaveFormNotSubmitted] = useState(false);
  const [isLoadingSave, setIsLoadingSave] = useState<boolean>(false);
  const [isLoadingSubmit, setIsLoadingSubmit] = useState<boolean>(false);
  const [isTypeForm, setIsTypeForm] = useState<'save' | 'submit'>();
  const ReviewTicketServices = useReviewServices();
  useEffect(() => {
    if (isShowNoti) {
      Alert.alert(t('CM.noti'), t('CM.pleaseFillForm'));
    }
  }, []);

  const getValueDataForm = async (version: any) => {
    const body = {
      userTaskId: data._userTaskId,
      ticketId: data._ticketId,
      version: version,
    };
    const dataResp =
      await ReviewTicketServices.getFormVersionByTicketAndUserTaskId(
        body,
        accessToken,
      );
    if (dataResp) {
      if (version === null) {
        const listForm = Array.from(
          Array(Math.ceil(dataResp.versionUserTask)),
        ).map((item, idx) => idx + 1);
        const filterList = listForm.map((i, index) => ({
          id: i,
          key: `${t('CM.version')} ` + i,
          value: `${t('CM.version')} ` + i,
        }));
        setListFrom(filterList);
        setVersionFromSelect(filterList[filterList.length - 1]);
      }
    }
    return dataResp;
  };

  const handleOnMessage = async (message: WebViewMessageEvent) => {
    if (viewFromDoc) return;
    const dataWebview = message.nativeEvent.data as any;
    if (!dataWebview || typeof dataWebview !== 'string') return;
    const dataWebviewJson = JSON.parse(dataWebview);
    if (dataWebviewJson?.result === 'e-from') {
      patchValue(null);
    } else if (dataWebviewJson?.result === 'result') {
      try {
        if (isTypeForm === 'save') {
          setIsLoadingSave(true);
        } else {
          setIsLoadingSubmit(true);
        }
        const newData = dataWebviewJson?.data.map((item: any) => ({
          ...item,
          dataType: convertTypeField(item),
          typeForm: item.type,
          value: item.value,
        }));

        const body = {
          formData: newData,
          ticketId: data._ticketId,
          userTaskId: data._userTaskId,
          version: versionId,
          eformId: data.formId,
        };
        const dataResp = await ReviewTicketServices.updateFormData(
          body,
          accessToken,
        );
        if (dataResp) {
          Toast.show({
            type: 'success',
            text1: t('CM.noti'),
            text2: t('CM.fillFormSuccess'),
          });
          if (!isSaveFormNotSubmitted) {
            handleSubmit && handleSubmit();
          }
          onClose();
        } else {
          Toast.show({
            type: 'error',
            text1: t('CM.noti'),
            text2: t('CM.fillFormFailure'),
          });
        }
      } catch (error) {
      } finally {
        setIsLoadingSave(false);
        setIsLoadingSubmit(false);
      }
    }
  };

  const convertTypeField = (item: any) => {
    if (
      [
        'textField',
        'textArea',
        'password',
        'email',
        'phone_number',
        'radio_button',
        'upload_file',
        'upload_img',
        'hyperlink',
        'datepicker',
        'signature',
      ].includes(item.type)
    ) {
      return 'String';
    } else if (item.type === 'number') {
      return 'Number';
    }
    //  else if (item.type === 'datepicker') {
    //   return 'Date';
    // }
    else if (['check_box', 'dropdown'].includes(item.type)) {
      if (
        (item.type === 'dropdown' && item.validate.mutilChoose) ||
        item.type === 'check_box'
      ) {
        return 'List';
      }
      return 'String';
    }
    return null;
  };

  const patchValue = async (version: any) => {
    const data = await getValueDataForm(version);
    if (data) {
      let dataFrom = data?.formData;
      if (dataFrom && envelopeEnvNo) {
        dataFrom.forEach((field: any) => {
          if (field.code === 'eContractEnvelopEnvNo') {
            field.value = envelopeEnvNo;
          }
        });
      }
      dataFrom = dataFrom?.filter((d: any) => d.value);
      const formData = JSON.stringify({
        type: 'postFormEdit',
        isReadonly: readOnly,
        data: dataFrom,
      });
      const scriptJSX = `window.postMessage('${formData
        .replace(/\\/g, '\\\\')
        .replace(/'/g, "\\'")}', '*');`;
      webviewRef?.current?.injectJavaScript(scriptJSX);
    }
  };

  const renderDropdown = () => {
    return (
      <View style={styles.version}>
        <TextCM>{versionFromSelect?.value}</TextCM>
      </View>
    );
  };

  const handleSaveInfo = () => {
    setIsSaveFormNotSubmitted(true);
    setIsTypeForm('save');
    const dataSubmit = JSON.stringify({
      result: 'submit',
    });
    const scriptJSX = `window.postMessage('${dataSubmit}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
  };

  const handleSaveInfoAndSubmit = () => {
    setIsSaveFormNotSubmitted(false);
    setIsTypeForm('submit');
    const dataSubmit = JSON.stringify({
      result: 'submit',
    });
    const scriptJSX = `window.postMessage('${dataSubmit}', '*');`;
    webviewRef?.current?.injectJavaScript(scriptJSX);
  };

  return (
    <SafeAreaView
      edges={['bottom', 'top']}
      style={{flex: 1, backgroundColor: '#fff'}}>
      <HeaderCM
        style={{
          backgroundColor: '#fff',
        }}
        titleStyle={{
          color: '#00204D',
        }}
        fillIconBackLeft="#00204DB2"
        contentStyle={{
          backgroundColor: '#fff',
        }}
        title={nameHeader}
        onPressIconLeft={onClose}
      />
      <Divider />
      {listFrom.length > 0 && !isOnlyView && !readOnly && (
        <View
          style={{
            padding: 16,
            alignItems: 'center',
            flexDirection: 'row',
            justifyContent: 'space-between',
            paddingHorizontal: SizeDP(16),
          }}>
          <TextCM>{t('CM.selectVersion')}</TextCM>
          <OverflowMenuCM
            selectedIndex={selectedIndex}
            listData={listFrom}
            setSelectedIndex={(item: IndexPath | undefined) => {
              if (item) {
                setSelectedIndex(item);
                setVersionFromSelect(listFrom[item?.row]);
                patchValue(listFrom[item?.row]?.id);
              }
            }}
            selectedTitle={renderDropdown()}
          />
        </View>
      )}
      <WebView
        nestedScrollEnabled={true}
        ref={webviewRef}
        onMessage={handleOnMessage}
        startInLoadingState
        injectedJavaScript={String.SCRIPT_SCALE_WEBVIEW}
        allowsInlineMediaPlayback={true}
        mediaPlaybackRequiresUserAction={false}
        domStorageEnabled={true}
        javaScriptEnabled={true}
        thirdPartyCookiesEnabled={true}
        source={{
          uri: getConfig().BASE_URL_FORM + versionId,
        }}
        onLoadEnd={() => setIsDisable(false)}
        webviewDebuggingEnabled
      />
      <View style={styles.ctnFooter}>
        {!isOnlyView && !readOnly && (
          <ButtonCM
            disabled={isDisable}
            style={{flex: 1, backgroundColor: '#f2ecfd'}}
            styleLabel={{
              color: '#6d1fad',
            }}
            label={t('CM.save')}
            loading={isLoadingSave}
            onPress={handleSaveInfo}
          />
        )}
        {!isOnlyView && !readOnly && (
          <ButtonCM
            label={t('CM.confirm')}
            style={{flex: 1}}
            styleLabel={{
              color: '#fff',
            }}
            loading={isLoadingSubmit}
            onPress={handleSaveInfoAndSubmit}
          />
        )}
      </View>
    </SafeAreaView>
  );
};

export default ViewFormScreen;
